import React from 'react';
import node from '../img/nodejs.jpg';
import react from '../img/react.jpeg';
import php from '../img/php.png'

 
class Jumbotron extends React.Component {
 
  render() {
 
    return (
 
        <div className="jumbotron">
            <div className="container">
                <h1 className="display-1"></h1>
                <figure>
                  <blockquote class="blockquote">
                    <h1>Lenguajes o Frameworks con los que he trabajado</h1>
                      </blockquote>
                            <figcaption class="blockquote-footer">
                                      aprendidos de manera <cite title="Source Title">autodidacta</cite>
                              </figcaption>
                  </figure>
                <div className="album py-5 bg-light">
				    <div className="container">
				      <div className="row">
				        <div className="col-md-4">
				          <div className="card mb-4 shadow-sm">
                    <img className="bd-placeholder-img card-img-top" width="100%" height="225%" preserveAspectRatio="xMidYMid slice" focusable="false" src={node} />
				            <div className="card-body">
				              <p className="card-text">Programa realizado para un restaurante, en el framework de Nodejs Express.</p>
				              <div className="d-flex justify-content-between align-items-center">
				                <div className="btn-group">
                        <a className="btn btn-sm btn-outline-secondary" type="button" href="#"  role="button">Github</a>
                        <a className="btn btn-sm btn-outline-secondary" type="button" href="https://nodejs.org/es/"  role="button">Leer</a>
				                </div>
				                
				              </div>
				            </div>
				          </div>
				        </div>
                <div className="col-md-4">
				          <div className="card mb-4 shadow-sm">
                  <img className="bd-placeholder-img card-img-top" width="100%" height="225%" preserveAspectRatio="xMidYMid slice" focusable="false" src={react} />
				            <div className="card-body">
				              <p className="card-text">Pagina en la cual estas ahora, revisando mi portafolio.</p>
				              <div className="d-flex justify-content-between align-items-center">
				                <div className="btn-group">
                        <a className="btn btn-sm btn-outline-secondary" type="button" href="#"  role="button">Github</a>
                        <a className="btn btn-sm btn-outline-secondary" type="button" href="https://es.reactjs.org"  role="button">Leer</a>
				                  
				                </div>
				                
				              </div>
				            </div>
				          </div>
				        </div>
				        <div className="col-md-4">
				          <div className="card mb-4 shadow-sm">
                  <img className="bd-placeholder-img card-img-top" width="100%" height="225%" preserveAspectRatio="xMidYMid slice" focusable="false" src={php} />
				            <div className="card-body">
				              <p className="card-text">Programa funcional realizado para una materia .</p>
				              <div className="d-flex justify-content-between align-items-center">
				                <div className="btn-group">
                        <a className="btn btn-sm btn-outline-secondary" type="button" href="#"  role="button">Github</a>
                        <a className="btn btn-sm btn-outline-secondary" type="button" href="https://www.php.net"  role="button">Leer</a>
				                  
				                </div>
				                
				              </div>
				            </div>
				          </div>
				        </div>
 
				       
                        </div>
            </div>
        </div>
       
                        </div>
        </div>
        
 
    )
    
  }
 
}
 
export default Jumbotron;